    // ğŸ†• æ–°å¢ï¼šåŠ è½½é¡µé¢æ•°æ®çš„ç»Ÿä¸€æ–¹æ³•
    async loadPageData() {
      if (!this.openid) {
      		  // å¼¹å‡ºç™»å½•æç¤ºæ¡†
      		  uni.showModal({
      		    title: 'æç¤º',
      		    content: 'éœ€è¦ç™»å½•æ‰èƒ½ä½“éªŒæ›´å¤šå†…å®¹å“¦',
      		    cancelText: 'å–æ¶ˆ',
      		    confirmText: 'ç™»å½•',
      		    success: (res) => {
      		      if (res.confirm) {
      		        // ç”¨æˆ·ç‚¹å‡»äº†"ç™»å½•"æŒ‰é’®
      		        uni.switchTab({
