            try {
              this.userUnreadList = JSON.parse(userData.unread);
            } catch {
              this.userUnreadList = [];
            }
          } else if (Array.isArray(userData.unread)) {
            this.userUnreadList = userData.unread;
          } else {
            this.userUnreadList = [];
