    getUnreadCount(chatId) {
      if (!this.userUnreadList || this.userUnreadList.length === 0) {
        return 0;
      }
      const chatUnread = this.userUnreadList.find(item => item.chat_id === chatId);
      return chatUnread ? chatUnread.unread : 0;
    },

